import { Component } from '@angular/core';
import { ViewController,IonicPage,  NavController,Events , ActionSheetController, NavParams } from 'ionic-angular';
import {   FormBuilder,  FormGroup,   Validators } from '@angular/forms';
import { Auth,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-tarifad-create',
  templateUrl: 'tarifad-create.html',
})
export class TarifadCreatePage {
  public form                  : FormGroup;
  username: string = "";
  public nidtarifa                 : any;
  public idtarifa                 : any;
  public nombre                 : any;
  public horaini                 : any;
  public horafin	     : any;
  public monto		     : any;
  public estado	     : any;
  idempresa:any;
  public vgrupo  :any=[];
 
  private _ID                  : String;

  public pageTitle           	: string;
  createdCode = null;
  scannedCode = null;

  isDisablednit :any;
  constructor(  public storage: Storage,  public msj: NotificationProvider, public translateService: TranslateService,public viewCtrl: ViewController,public authService:  Auth,public navCtrl 		: NavController,  
    public navParams 	: NavParams,   private _FB          : FormBuilder,         public actionSheetCtrl: ActionSheetController, 
          public events: Events)
  {
    this.translateService.get('APPX').subscribe((value) => {
      this.idempresa=value
     
  })
     this.form = this._FB.group({
        nombre       : ['', Validators.required],
        horaini       : ['', Validators.required],
        horafin       : ['', Validators.required],
        monto       : ['', Validators.required],
        estado       : ['', Validators.required]
      
     });

    
  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }

  ionViewDidLoad() : void
  {this.idtarifa=this.navParams.data.record2.idtarifa;
    this.nidtarifa=this.navParams.data.record2.nidtarifa;
    
    if(this.navParams.get("record")==null)
    {
       
      this.translateService.get('ITEM_CREATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
      
      this.isDisablednit =true
    }
    else {
     
       this._ID 				=	this.navParams.data.record._id;
       this.idtarifa		=	this.navParams.data.record.idtarifa;
       this.nombre		=	this.navParams.data.record.nombre;
       this.horaini		=	this.navParams.data.record.horaini;
       this.horafin		=	this.navParams.data.record.horafin;
       this.monto		=	this.navParams.data.record.monto;
       this.estado		=	this.navParams.data.record.estado;
   
      
       this.isDisablednit =false
       this.translateService.get('ITEM_UPDATE_TITLE').subscribe((value) => {
        this.pageTitle=value;
      });
    

    }
  }

  limpia(op:string) : void
  {
    this.form.controls['nombre'].setValue('');
    this.form.controls['horaini'].setValue(op);
     this.form.controls['horafin'].setValue(op);
     this.form.controls['monto'].setValue(op);
     this.form.controls['estado'].setValue(op);
  
  }
    

 
  

  manageGallery() : void
  {
    
  let creat='Actualiza';
    if(this.navParams.get("record")==null)
    {  creat='Crea';
    }
   


     let   options       : any	     = { 
       idempresa:this.idempresa,
      idtarifa : this.idtarifa,
     nombre : this.form.controls['nombre'].value,
     horaini : this.form.controls['horaini'].value,
     horafin : this.form.controls['horafin'].value,
     monto : this.form.controls['monto'].value,
     estado : this.form.controls['estado'].value,
     bitacora:{idempresa:this.idempresa,idafiliado:'',email: this.username,permiso:creat,accion:creat+' dtarifas '}
    };


     if(this.navParams.get("record")==null)
     {  
          if(options){
            this.authService.createReg('',options,'dtarifas').then((result) => {
                 if (!this.form.valid) { return; }
                 this.viewCtrl.dismiss(result);
         
          }, (err) => {  
            this.msj.showError3('Error',err.error);
          });
          }
     }   
     else
     {
      if(options){
        this.authService.createReg(this._ID,options,'dtarifas').then((result) => {
           if (!this.form.valid) { return; }
           this.viewCtrl.dismiss(result);
       }, (err) => {
        this.msj.showError3('Error',err.error);
       });
    }}
  }
  stringify(country){
    return JSON.stringify(country);
  }
 
  cancel() {
    this.viewCtrl.dismiss();
  }
  done() {
    if (!this.form.valid) { return; }
    //this.viewCtrl.dismiss(this.form.value);
  }
  optionsFn(item) {//here item is an object 
    console.log(item);
  }


}



